.message {
  margin-bottom: 20px;
}
.message-body {
  max-width: 80%;
  display: inline-block;
  padding: 20px;
  background-color: #eee;
  border: 1px;
  border-radius: 5px;
  padding-right: 50px;
}

.username {
  font-weight: bold;
  font-size: 0.9rem;
  margin-bottom: 5px;
}

.username-container {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
  margin-top: 10%;
}

h6 {
  display: block;
}
